#menutitle {
  text-transform: uppercase;
  font-weight: lighter;
  display: flex;
  margin-left: 1em;
  margin-block-start: 1;
  margin-block-end: 0;
  padding-bottom: 0;
  max-width: 30vw;

  @media screen and (min-width: 700px) {
    // padding-left: 3em;
    margin-left: 2em;
  }
}

body {
  display: flex;
  flex-direction: column;
}

h2 {
  text-transform: uppercase;
  font-weight: lighter;
  font-size: 1.8em;
}

p {
  font-weight: lighter;
}

.menucontainer {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  margin: 2em;
  max-width: 100vw;

  @media screen and (min-width: 700px) {
    display: flex;
    flex-flow: row wrap;
    // margin: 0 auto;
    max-width: 90vw;
    justify-content: space-between;
  }

  // h2 {
  //     padding-top: 2em;
  // }

  .menucategory {
    display: flex;
    flex-direction: column;
    width: 70vw;

    @media screen and (min-width: 700px) {
      flex-direction: column wrap;
      align-content: space-between;
      width: 30vw;
      margin: 2em;
    }
    h2 {
      padding-top: 1.5em;
      @media screen and (min-width: 700px) {
        padding-top: 0em;
      }
    }
    .firsth2 {
      padding-top: 0;
      margin-top: 0;
    }
    .menuitem {
      width: 70vw;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      @media screen and (min-width: 700px) {
        width: 35vw;
        padding: 1em 0;
      }
    }
  }
}

.sectiontitle {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  margin: 0 2em;
  max-width: 100vw;
  h2 {
    margin: 0;
  }

  @media screen and (min-width: 700px) {
    // display: block;
    max-width: 70vw;
    // border-left: 2em;
    padding-left: 2em;
    margin-left: 2em;
  }
}

.menu__image {
  width: 80vw;
  margin: 0 auto;
  margin-bottom: -1em;
  z-index: 1;
  @media (min-width: 767px) {
    width: 70vw;
  }
}
